import React, { useState } from "react";
import Title from "../../packages/Title";
import Message from "../../packages/Message";
import Button from "../../packages/Button";

export default function MessageExample() {
  const [show1, setShow1] = useState<boolean>(false);
  const [show2, setShow2] = useState<boolean>(false);
  const [show3, setShow3] = useState<boolean>(false);
  const [show5, setShow5] = useState<boolean>(false);
  return (
    <div className="flex column gap-24">
      <div className="flex column gap-12">
        <Title title="success" type="h3" />
        <div className="flex justify-center gap-24 border p-32">
          <Button onClick={() => setShow1(!show1)}>success</Button>
          <Message type="success" text="success" show={show1} />
          <Button onClick={() => setShow2(!show2)}>fail</Button>
          <Message type="fail" text="fail" show={show2} />
          <Button onClick={() => setShow3(!show3)}>warn</Button>
          <Message type="warn" text="warn" show={show3} />
        </div>

        <Title title="" type="h3" />
        <div className="flex justify-center gap-24 border p-32">
          <Button onClick={() => setShow5(!show5)}></Button>
          <Message text="" show={show5} />
        </div>
      </div>
    </div>
  );
}
